  .gallery-slider__slide {
    max-width: 300px;
  }
}

.gallery-slider__full-screen-link {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.gallery-slider__slide-image {
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery-slider__controls {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  width: 100%;
  height: min-content;
  max-width: 500px;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
